body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.buttondiv {
  margin: 0 20px;
  color: rgba(0, 0, 0, 0.87);
  background-color: transparent;
  -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  display: inline-block;
}

.micbutton {
  border: 10px;
  background-color: rgb(224, 224, 224);
  box-sizing: border-box;
  display: inline-block;
  font-family: Roboto, sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  text-decoration: none;
  margin: 0px;
  padding: 0px;
  outline: none;
  font-size: inherit;
  font-weight: inherit;
  position: relative;
  vertical-align: bottom;
  -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  height: 56px;
  width: 56px;
  overflow: hidden;
  border-radius: 50%;
  text-align: center;
}

.insidemicbutton {
  -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  top: 0px;
}

.mic {
  display: inline-block;
  color: rgb(255, 255, 255);
  fill: rgb(255, 255, 255);
  height: 56px;
  width: 24px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  line-height: 56px;
}

.active {
  background-color: rgb(255, 64, 129);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 10px,
              rgba(0, 0, 0, 0.23) 0px 3px 10px;
}
.App {
  text-align: center;
}



.App-logo {
  display: inline;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 3vw;
}

.App-header {
  background-color: #2f5f6b;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.navbar {
 
  margin-top: auto;
  
}
#top-filler {
  margin-bottom: auto;
}

#nav-container {
  align-items: flex-end;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body {
  margin: 0;
}

/* Global */
.item-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.single-item {
  display: inline-block;
  padding: 0;
  margin: 0;
  height: 450px;
  margin: 15px 12px;
  width: 31%;
}

.single-item img {
  width: 100%;
  height: 350px;
  position: relative;
}

.single-item h3 {
  font-size: 22px;
  margin: 0;
}

.item-footer {
  width: 100%;
  background-color: #4d4d4d;
  color: white;
  padding: 10px;
}

.item-footer a {
  color: white;
  margin-top: 8px;
  background-color: #ff6666;
}

.view-more {
  border: 1px solid black;
}

/* Home Page */
.home-container {
  background-image: url("https://static.pexels.com/photos/167605/pexels-photo-167605.jpeg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height: 100vh;
}

.home-form {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 60%;
  text-align: center;
}

.home-search {
  margin-top: 10px;
  background-color: black;
  border-radius: 0;
  color: white;
  background-color: #ff6666;
  border: 1px solid #ff6666;
}

.home-search:hover, .home-search:active {
  color: #ff6666;
  background-color: white;
  border: 1px solid #ff6666;
}

.form-wrapper {
  background-color: rgba(255, 255, 255, 0.6);
  width: 50%;
  height: 200px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.artist-container {
  padding: 10px 40px;

}

.artist-img {
  width: 100%;
}

.artist-info-container {
  background-color: #4D4D4D;
  text-align: center;
  padding-bottom: 10px;
}

.artist-info-container p {
  color: #fff;
  font-size: 18px;
  margin: 0;
}

.artist-info-container a {
  color: #fff;
  font-size: 12px;
  background-color: #F86967;
  border-radius: 5px;
  padding: 4px 10px;
}

.track-item {
  background-color: #F6F6F6;
  margin-bottom: 2px;
  padding: 10px 0;
}

.track-item p {
  margin-bottom: 0;
}

.header-row {
  background-color: #4A4A4A;
  padding: 20px 10px;
  color: #F6F6F6;
  font-size: 20px;
}

.tracks {
  margin: 0 5%;
  font-size: 1.5em;
}

.tracks-title {
  display: block;
  margin: 3%;
  background-color: grey;
  color: white;
}

.twistit {
  background: #fd4282;
  color: #fff;
}


